<template>
    <div class="section">
      <el-date-picker
        v-model="date"
        type="month"
        format="yyyy 年 MM 月"
        value-format="yyyy-MM"
        @change="getData"
        placeholder="选择月份">
        </el-date-picker>
        <div class="tabs">
            <div :class="{active:tab == 1}" @click="tab = 1">按照井号统计</div>
            <div :class="{active:tab == 2}" @click="tab = 2">按照队伍统计</div>
        </div>
        <el-table :data="well" border style="width: 100%"  v-show="tab == 1">
            <el-table-column prop="well_no" label="井号" />
            <el-table-column prop="team" label="井队号" />
            <el-table-column label="基础信息">
                <el-table-column prop="well_type" label="井别" />
                <el-table-column prop="well_shape" label="井型" />
                <el-table-column prop="depth" label="井深（m）" />
            </el-table-column>
            <el-table-column prop="completion_layer" label="完钻层位" />
            <el-table-column label="时间">
                <el-table-column prop="start_at" label="开钻日期" />
                <el-table-column prop="drilling_finish_at" label="完钻日期" />
                <el-table-column prop="completion_finish_at" label="完井日期" />
            </el-table-column>
            <el-table-column label="周期">
                <el-table-column prop="drilling_period" label="钻井周期" />
                <el-table-column prop="completion_period" label="完井周期" />
            </el-table-column>
            <el-table-column prop="rop" label="机械钻速（m/h）" />
            <el-table-column prop="accident_ratio" label="事故复杂率%" />
            <el-table-column prop="well_quality" label="井身质量评价" />
            <el-table-column prop="cementation_quality" label="固井质量评价" />
            <el-table-column prop="avg_collection" label="取芯收获率%" />
            <el-table-column prop="target_count" label="靶点数" />
        </el-table>
        <el-table :data="team" border style="width: 100%" v-show="tab == 2">
            <el-table-column prop="team" label="队号" />
            <el-table-column prop="project" label="项目部" />
            <el-table-column prop="start_drill_count" label="开钻井数" />
            <el-table-column prop="start_drill_no" label="开钻井号" />
            <el-table-column prop="completion_count" label="完井井数" />
            <el-table-column prop="completion_no" label="完井井号" />
            <el-table-column prop="acc_footage" label="累计进尺（m）" />
            <el-table-column prop="avg_completion_depth" label="平均完钻井深（m）" />
            <el-table-column prop="max_completion_depth" label="最大完钻井深（m）" />
            <el-table-column prop="drilling_period" label="平均钻井周期（d）" />
            <el-table-column prop="completion_period" label="平均完井周期（d）" />
            <el-table-column prop="rop" label="平均机械钻速（m/h）" />
            <el-table-column prop="accident_ratio" label="事故复杂率" />
        </el-table>
    </div>
</template>

<script>
import request from "@/utils/request";
export default {
  props: ["route"],
  data() {
    return {
      tab: 1,
      date: '2018-07',
      team: [],
      well: []
    };
  },

  components: {},

  computed: {},

  mounted: function() {
    this.getData()
  },

  methods: {
    getData() {
      let params = this.route;
      request({
        url: "/report/month",
        method: "get",
        params: {
          ...params,
          date: this.date
        }
      }).then(res => {
        this.team = res.team;
        this.well = res.well;
      });
    }
  }
};
</script>



